import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    players: 0,
  },
  mutations: {
    GET_SERVER_INFO(state, payload) {
      state.players = payload;
    },
  },
  actions: {
    async getServerInfo({ commit }) {
      const response = await axios.get(
        "https://api.top-serveurs.net/v1/servers/KIY2GAPDOC/full"
      );
      commit("GET_SERVER_INFO", response.data.players);
    },
  },
  modules: {},
});
